<template>
    <div class="flex col-12 px0" v-if="ready">
        <div class="col-8">
            <v-data-table
                :headers="headers"
                :items="data"
                :rows-per-page-items="[15]"
                class="elevation-1"                
                disable-initial-sort
                show-select              
                item-key="item_code"               
            >          
            <template v-slot:items="props">                     
                <tr>
                    <td class="click_row" @click="view_details(props.item.item_code)"><u>Details</u></td>
                    <td class="click_row" @click="view_details(props.item.item_code)">{{ props.item.item_code }}</td>
                    <td class="click_row" @click="view_details(props.item.item_code)">{{ props.item.barcode }}</td>
                    <td class="click_row" @click="view_details(props.item.item_code)">{{ props.item.eng_name }}</td>
                    <td class="click_row" @click="view_details(props.item.item_code)">{{ props.item.location }}</td>
                    <td style="padding-left:0"><input type="checkbox" class="form-control" v-model="selected" :value="props.item.item_code"/></td>
                </tr>
                
                <template v-slot:pageText="props">
                    Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                </template>  
            </template>
            </v-data-table>
        </div>
        <div class="col-4">
            <div class="card" style='height:auto'>
                <div class="card-header" style='height:4rem!important;padding-top:1rem!important'>
                    <strong>Control Panel</strong>
                </div>            
                <div class="card-body card-block">
                    <div class="col-12 px0 cprow">
                        <input type="text" v-model="key" class="form-control" placeholder="Search Record(s)" @keyup="fetch_inv_list"/>
                    </div>
                    <div class="col-12 px0 cprow" style='margin-bottom:0.5rem!important;'>
                        <label><b><u>Filter(s)</u></b></label>
                    </div>
                    <div class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Section
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <select class="form-control select" v-model="section" @change="change_grp">
                                <option value="All">All</option>
                                <option value="S/">Secondary section</option>
                                <option value="P/">Primary section</option>
                                <option value="K/">Kindergarten section</option>
                            </select>
                        </div>
                    </div>
                    <div class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Group
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <select class="form-control select" v-model="grp" @change="fetch_inv_list">
                                <option value="All">All</option>
                                <option  v-for="g in grp_list.length" :value="grp_list[g-1].groupcode">{{ grp_list[g-1].group_description}}</option>
                            </select>
                        </div>
                    </div>
                    <div class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Block
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <select class="form-control select" v-model="blk" @change="fetch_inv_list">
                                <option value="All">All Blocks</option>
                                <option value="WHC">Whole Campus</option>                               
                                <option v-for="n in 8" :value="blks[n-1]">Block {{ blks[n-1] }}</option>
                                <option value="SP">Special</option>
                                <option value="OC">Common Area</option>
                            </select>
                        </div>
                    </div>
                    <div class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Floor
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <select class="form-control select" v-model="flr" @change="fetch_inv_list">
                                <option value="All">All</option>
                                <option value="WHC">Whole Campus</option>
                                <option value="G">Ground floor</option>
                                <option v-for="f in 8" :value="f"> {{ f }}<sup>{{ index_sup[f-1] }}</sup> floor</option>
                                <option value="SP">Special</option>
                            </select>
                        </div>
                    </div>
                    <div class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Category
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <select class="form-control select" v-model="category"  @change="fetch_sub_category">
                                <option value="All">All</option>
                                <option v-for="l in category_list.length" :value="category_list[l-1].main_category">{{ category_list[l-1].main_cat_name }}</option>
                            </select>
                        </div>
                    </div>
                    <div class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Sub-Category
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <select class="form-control select" v-model="sub_category"  @change="fetch_inv_list">
                                <option value="All">All</option>
                                <option v-for="l in sub_category_list.length" :value="sub_category_list[l-1].sub_category">{{ sub_category_list[l-1].sub_cat_name }}</option>
                            </select>
                        </div>
                    </div>
                    <div class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Type
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <select class="form-control select" v-model="scope"  @change="fetch_inv_list">
                                <option value="All">All Item(s)</option>
                                <option value="single">Single Item(s)</option>
                                <option value="bulk">Bulk Item(s)</option>
                            </select>
                        </div>
                    </div>
                    <div v-if="selected[0]!=undefined" class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Reason
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <textarea class="form-control" v-model="reason" placeholder="Fill in write-off reason" rows="3"></textarea>
                        </div>
                    </div>
                </div>
                <div class="card-footer">
                    <div class="flex">
                        <div>
                            <button type="button" class="btn btn-primary btn-sm" @click="new_record">
                                <i class="fa fa-plus-circle"></i> New Record
                            </button>
                            <button type="button" class="btn btn-danger btn-sm" @click="reset">
                                <i class="fa fa-refresh"></i> Reset
                            </button>
                        </div>
                        <div style="margin-left:auto">
                            <button type="button" class="btn btn-danger btn-sm" @click="batch_process">
                                <i class="fa fa-times"></i> Batch Write Off
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            reason:'',
            ready:false,
            data:[],
            singleSelect: false,
            selected:[],
            headers: [
                { text: 'Details',value :'item_code',sortable: false},
                { text: 'Item Code',value :'item_code',sortable: true},
                { text: 'Barcode',value:'barcode',sortable: true},
                { text: 'Name',value:'eng_name',sortable: true},                
                { text: 'Location',value:'location',sortable: true},
                { text: 'Batch Process',value:'item_code',sortable: false},
            ],
            section:'All',
            grp_list:[],
            grp:'All',
            room_list:[],
            room:'All',
            category_list:[],
            category:'All',
            sub_category_list:[],
            sub_category:'All',
            scope:'All',
            blk:'All',
            blks:['A','B','C','D','E','F','G','H'],
            flr:'All',
            index_sup:['st','nd','rd','th','th','th','th','th'],
            type:'All',
            key:'',

        }
    },
    async created(){
        await this.fetch_inv_list();
        await this.load_filter();
        this.ready = true;
    },
    async mounted(){

    },
    methods:{
        async change_grp(){
            await this.fetch_inv_list();
            await this.load_filter();
        },
        batch_process(){
            if(this.selected[0]!=undefined){
                if(this.reason!=''){
                    this.$confirm('Confirm Write Off : '+ this.selected+ ' ?')
                        .then(
                            ()=>{
                                var formData = new FormData();
                                formData.append('item_code',this.selected);
                                formData.append('type',this.scope);
                                formData.append('reason',this.reason);
                                axios.post('inventory/web/backend/batch_process.php',formData)
                                    .then(
                                        (response)=>{
                                            if(response.data=='OK'){
                                                this.$alert('Successfully Applied Write-off for '+this.selected)
                                                this.reason = '';
                                                this.selected = [];
                                                this.fetch_inv_list();                                               
                                            } else {
                                                console.log(response.data);
                                            }
                                        }
                                    )
                            }
                        )
                } else {
                    this.$alert('Enter write-off reason');
                }
            } else {
                this.$alert('Please select at least one record');
            }
        },
        new_record(){
            this.$router.push('insert_item');
        },
        async fetch_inv_list(){
            var formData = new FormData();
            formData.append('section',this.section);
            formData.append('blk',this.blk);
            formData.append('floor',this.flr);
            formData.append('school',this.grp);
            formData.append('scope',this.scope);
            formData.append('key',this.key);
            formData.append('category',this.category);
            formData.append('sub_category',this.sub_category);
            await axios.post('inventory/web/backend/fetch_inv_list.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.data = response.data;
                        } else {
                            this.data=[];
                        }
                    }
                )
        },
        async load_filter(){
            var formData = new FormData();
            formData.append('section',this.section);
            await axios.post('inventory/web/backend/filter_preload.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.category_list = response.data.category;
                            this.grp_list = response.data.grp_code;
                            this.grp = 'All';
                        }
                    }
                )
        },
        async view_details(id){
            document.getElementById('query').value = this.section
            document.getElementById('tmp_id').value = id;
            document.getElementById('tmp_id_2').value = this.scope;
            this.$router.push('edit_item');
        },
        async fetch_sub_category(){
            var formData = new FormData();
            formData.append('category',this.category);
            await axios.post('inventory/web/backend/fetch_sub_category.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.sub_category_list = response.data;
                            this.sub_category = 'All';
                        } else {
                            this.sub_category_list = [];
                            this.sub_category = 'All';
                        }
                    }
                )
            await this.fetch_inv_list();
        },
        async initialize(){
            this.category = 'All';
            this.blk = 'All';
            this.flr = 'All';
            this.grp = 'All';
            this.key = '';
        },
        async reset(){
            await this.initialize();
            await this.fetch_inv_list();
        }
    }
}
</script>
<style scoped>
.flex{
    display:flex;
    flex-direction:row;
}
.px0{
    padding-left:0;
    padding-right:0;
}
.click_row{
    cursor: pointer;
}
u {
    color:rgba(0,0,0,0.6);
}
.cprow{
    margin-bottom:1rem;
}
td{
    text-transform: capitalize!important;
}
label{
    padding-top:0.35rem!important;
}
</style>


